
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.content {
    padding: 0 20px;
}

.centered {
    text-align: center;
}

.right-aligned {
    text-align: right;
}

.button-secondary {
    background: rgb(66, 184, 221);
}

.button-danger {
    background: rgb(202, 60, 60); /* this is a maroon */
}

.add-controls {
    position: fixed;
    bottom: 0;
    left: 0;
    height: 6em;
    width: 100%;
    background-color: #ddd;
}

.add-controls button {
    width: 100%;
    height: 3em;
}

.title-controls {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2.5em;
    width: 100%;
}

.title-controls button {
    width: 100%;
    height: 2.5em;
}

#login-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: white;
    z-index: 100;
}

.login-container {
    margin-top: 12em;
    text-align: center;
}

@media (min-width: 48em) {
    .add-controls {
        position: fixed;
        bottom: 0;
        left: 0;
        height: 3em;
        width: 100%;
        background-color: #ddd;
    }

    .add-controls button {
        width: 100%;
        height: 3em;
    }
}

.active-season {
    background-color: #DDD;
}

.l-box {
    padding: 1em;
    margin: 0.5em;
    position: relative;
    border: 0.1em solid lightgray;
    border-radius: 2px;
    min-height: 200px;
    font-size: 1em;
}

.l-box b {
    text-transform: uppercase;
    margin: 0.75em auto 0.25em auto;
    font-size: 0.8em;
}

.add-title-item {
    cursor: pointer;
}

.add-title-item ion-icon {
    font-size: 5em;
}

.anime-item ion-icon {
    font-size: 1.2em;
}

/* Modal styles */

.modal_overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,0.6);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    z-index: 100;
}

.modal_container {
    background-color: #fff;
    padding: 0 20px;
    max-width: 500px;
    max-height: 100vh;
    border-radius: 4px;
    overflow-y: auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.modal_header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.modal_title {
    margin-top: 0;
    margin-bottom: 0;
    font-weight: 600;
    font-size: 1.25rem;
    line-height: 1.25;
    color: #00449e;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.modal_close {
    background: transparent;
    border: 0;
}

.modal_header .modal_close:before { content: "\2715"; }

.modal_content {
    margin-top: 1rem;
    margin-bottom: 1rem;
    line-height: 1.5;
    color: rgba(0,0,0,.8);
}

/**************************\
  Demo Animation Style
\**************************/

@-webkit-keyframes mmfadeIn {
    from { opacity: 0; }
      to { opacity: 1; }
}

@keyframes mmfadeIn {
    from { opacity: 0; }
      to { opacity: 1; }
}

@-webkit-keyframes mmfadeOut {
    from { opacity: 1; }
      to { opacity: 0; }
}

@keyframes mmfadeOut {
    from { opacity: 1; }
      to { opacity: 0; }
}

@-webkit-keyframes mmslideIn {
  from { -webkit-transform: translateY(15%); transform: translateY(15%); }
    to { -webkit-transform: translateY(0); transform: translateY(0); }
}

@keyframes mmslideIn {
  from { -webkit-transform: translateY(15%); transform: translateY(15%); }
    to { -webkit-transform: translateY(0); transform: translateY(0); }
}

@-webkit-keyframes mmslideOut {
    from { -webkit-transform: translateY(0); transform: translateY(0); }
    to { -webkit-transform: translateY(-10%); transform: translateY(-10%); }
}

@keyframes mmslideOut {
    from { -webkit-transform: translateY(0); transform: translateY(0); }
    to { -webkit-transform: translateY(-10%); transform: translateY(-10%); }
}

.micromodal-slide {
  display: none;
}

.micromodal-slide.is-open {
  display: block;
}

.micromodal-slide[aria-hidden="false"] .modal_overlay {
  -webkit-animation: mmfadeIn .3s cubic-bezier(0.0, 0.0, 0.2, 1);
          animation: mmfadeIn .3s cubic-bezier(0.0, 0.0, 0.2, 1);
}

.micromodal-slide[aria-hidden="false"] .modal_container {
  -webkit-animation: mmslideIn .3s cubic-bezier(0, 0, .2, 1);
          animation: mmslideIn .3s cubic-bezier(0, 0, .2, 1);
}

.micromodal-slide[aria-hidden="true"] .modal_overlay {
  -webkit-animation: mmfadeOut .3s cubic-bezier(0.0, 0.0, 0.2, 1);
          animation: mmfadeOut .3s cubic-bezier(0.0, 0.0, 0.2, 1);
}

.micromodal-slide[aria-hidden="true"] .modal_container {
  -webkit-animation: mmslideOut .3s cubic-bezier(0, 0, .2, 1);
          animation: mmslideOut .3s cubic-bezier(0, 0, .2, 1);
}

.micromodal-slide .modal_container,
.micromodal-slide .modal_overlay {
  will-change: transform;
}

/* End modal */